html {
    @include fluid-type($mobile-viewport-width, $minimum-viewport-width, $maximum-viewport-width, $mobile-size, $minimum-size, $maximum-size);
    @media (min-width: 1921px) {
        @include fluid-type($mobile-viewport-width, $maximum-viewport-width, $maximum-viewport-width-xl, $mobile-size, $maximum-size, $maximum-size-xl); } }
